* {
  margin: 0;
  padding: 0;
}

.App {
  font-family: "Quicksand";
}

.landing-page {
  background: linear-gradient(
      to bottom right,
      rgba(177, 252, 190, 0.6),
      rgba(82, 208, 255, 0.6),
      rgba(255, 162, 156, 0.6)
    ),
    url("https://images.jacobinmag.com/2014/11/17001408/12459375745_765fe7e151_k-1.jpg");
  background-size: cover;
  background-position: center center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: bold;
  height: 100vh;
}

.app-flex-title {
  font-weight: 900;
}

.giv-title {
  font-size: 7em;
  color: #fff;
  text-align: center;
}

.giv-slogan {
  margin: 0 0 200px 0;
  font-size: 3rem;
}

.nav-button {
  position: absolute;
  bottom: 10px;
}

@media screen and (max-width: 375px) {
  .landing-page {
    background-position: center center;
  }

  .giv-slogan {
    display: none;
  }
}

.footer-wrapper {
  background-color: black;
  color: white;
  font-family: QuickSand;
  text-align: center;
  font-weight: bold;
  font-size: 2vw;
}

.footer {
  padding: 2%;
}

.copyright {
  color: white;
  font-size: 0.9vw;
  font-family: Roboto;
}

a + a {
  margin-left: 2%;
}

.icons {
  padding-top: 1%;
  padding-bottom: 1%;
}

.icon {
  text-decoration: none;
  border-bottom: none;
  position: relative;
  color: white;
}

.icon:hover {
  color: #67dafae7;
}

.partner-wrapper {
  background-color: black;
  color: white;
  font-family: QuickSand;
  font-weight: bolder;
  text-align: center;
  font-size: 3vw;
}

.partner-wrapper header {
  padding: 2%;
}

.images {
  height: auto;
  width: 100%;
  padding: 1%;
}

.partner-wrapper img {
  height: auto;
  width: 40%;
}

.why-page {
  background: #b1fcbe;
}

.why-content {
  padding: 40px;
}

.why-title {
  font-size: 3rem;
  margin-bottom: 10px;
}

.why-text {
  font-size: 1.4rem;
}

.form-wrapper {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: center;
  font-weight: lighter;
  font-size: 3vw;
  padding: 4%;
}

.form-wrapper input {
  padding: 2px;
  width: 30%;
  border-color: black;
  border-width: 1px;
}

